.main3{
    display: flex;
    justify-content: center;
    background-color: white;
    padding: 30px 0px;

}

.main3 .main_images{
    display: flex;
    padding: 40px 0px;
}

.main3 .podcast_explain{
    display: flex;
    justify-content: center;
}
.main3 .podcast_explain h1{
    font-size: 35px;
}
.main3 .podcast_explain .yellow{
    color: rgba(245, 217, 140, 1)
}

.main3 .main_images{
    display: flex;
    justify-content: space-between;
}

.main3 .main_images .imageformat{
    background-color: black;
    color: white;
    padding-bottom: 15px;
    border-radius: 15px;
}
.main3 .main_images .imageformat h1{
    font-size: 22px;
    padding: 5px 5px;
    padding-left: 12px
}
.main3 .main_images .imageformat img:hover{
    filter: brightness(50%)
}
.main3 .main_images .imageformat img{
    border-radius: 15px;
}
.main3 .main_images .imageformat p{
    font-size: 13px;
    padding-left: 12px
}

