.header {
    display: flex;
    justify-content: center;
    padding: 20px 0px;
}

.header>div {
    display: flex;
    justify-content: space-between;
}

.header button {
    padding: 10px 30px;
    border-radius: 30px;
}

.header img {
    width: 80px
}

.header .btnLogin:hover {
    background-color: rgb(200, 200, 200)

}

.header .btnLogin {
    border: 0px;
    background: white;
    margin-right: 10px;
}

.header .btnSignUp:hover {
    background-color: rgb(236, 189, 58)
}
.header .btnSignUp {
    border: 0px;
    background-color: rgba(245, 217, 140, 1);
}