.main {
    background-color: rgba(255,251,235,0.5);
    display: flex;
    justify-content: center;
    padding: 50px 0px
}

.main>div {
    display: flex;
    justify-content: space-between;
}

.main .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.main .content .worldwide{
    display: flex;
}

.main .content .worldwide img{
    width: 30%;
    height: 66%;
    padding-top: 20px;
}

.main .content .worldwide .worlddes h2{
    margin-top: 20px;
    margin-left: 30px; 

}


.main .content .worldwide .worlddes p{
    margin-left: 30px; 
    padding-bottom: 10px;
}


.main .content .podcasts {
    border: 0px;
    color: rgba(245, 217, 140, 1);
}

.main .content button {
    border: 0px;
    padding: 10px 30px;
    border-radius: 30px;

}

.main .content .button_container{
    display: flex;
}
.main .content .btnStart {
    background-color: rgba(245, 217, 140, 1);
    padding-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main .content .btnStart:hover{
    background-color: rgb(236, 189, 58)
}

.main .content .btnExplore {
    margin-left: 15px;
    background-color: white;
    border: 2px solid lightgray;
    padding-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.main .content .btnExplore:hover{
    background-color: rgb(200, 200, 200)
}
.main .content .arrow{
    margin-right: 10px;
}

.main .content >p {
    padding: 20px 0px;
}

.main .place1 {
    display: flex;
    gap: 20px;
    padding-bottom: 20px;
    align-items: flex-end
}

.main .place2 {
    display: flex;
    gap: 20px;
    align-items: flex-start;
}