.main5{
    display: flex;
    justify-content: center;
    background-color: white;
    padding: 30px 0px;
}

.main5 .plan_explain {
    display: flex;
    justify-content: center;
    padding-bottom: 50px;
    font-size: 18px;
}

.main5 .plan_explain .yellow{
    color: rgba(245, 217, 140, 1);

}


.main5 .plan_box{
    display: flex;
    justify-content: center;
    gap: 35px
    
}
.main5 .plan_content{
    background-color: rgba(255,251,235,0.5);
    padding: 20px 30px;
}

.main5 .plan_content .text_format{
    display: flex;
    gap: 10px;
    padding-bottom: 1px;

}

.main5 .plan_content .pricing_box{
    border: 2.5px;
    border-style: solid;
    width: 70px;
    padding:0px 20px;
    border-radius: 15px;
}

.main5 .plan_content h2{
    padding: 25px 5px;
}

.main5 .plan_content h1{
    font-size: 20px;
    padding: 5px 5px;
}

.main5 .plan_content button:hover{
    background-color: rgb(236, 189, 58)
}

.main5 .plan_content button{
    display: flex;
    justify-content: center;

    border: 0px;
    font-size: 15px;
    padding: 10px 100px;
    border-radius: 30px;
    background-color: rgba(245, 217, 140, 1);
    margin-top: 20px;
}

