.main4 {
    background-color: rgba(255,251,235,0.5);
    display: flex;
    justify-content: center;
    padding: 50px 0px
}

.main4>div{
    display: flex;
}

.main4 .place1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 40%
}

.main4 .place1 h1 {
    font-size: 40px;
}

.main4 .place1 .yellow {
    color:rgba(245, 217, 140, 1)
}
.main4 .place1 p{
    padding: 15px 0px;
}
.main4 .place1 .checkmark{
    display: flex;
}

.main4 .place1 button:hover {
    background-color: rgb(236, 189, 58)
}
.main4 .place1 button {
    border: 0px;
    font-size: 20px;
    padding: 15px 40px;
    border-radius: 30px;
    background-color: rgba(245, 217, 140, 1);
}



.main4 .place1 .checkmark_box{
    display: flex;
    gap: 20px;
    padding-bottom: 20px;
}

.main4 .place1 .checkmark_box .checkmark svg{
    color:rgba(245, 217, 140, 1)
}

.main4 .place1 .checkmark_box .checkmark{
    gap:5px
}

.main4 .place2{
    display: flex;
    padding: 35px 0px;
    padding-left: 10px;
    gap:25px;
    margin-left: 70px
}
.main4 .place2 .first{
    margin-bottom: 40px;
}
.main4 .place2 .second{
    margin-top: 40px;
}

