.main6 {
    display: flex;
    justify-content: center;
    padding: 50px 0px
}
.main6 .background{
    background-color: rgba(255,251,235,0.5);
}
.main6 .background .touch_texts{
    width: 33.33%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.main6 .background .touch_texts h1{
    font-size: 37px;
}

.main6 .background .microphone{
    width: 33.33%;
    display: flex;
    justify-content: center;
}

.main6 .background .email{
    width: 33.33%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.main6 .background {
    display: flex;
    justify-content: center;
}

.main6 .background .microphone img{
    width: 50%;
}

.main6 .background .email .email_box{
    padding: 0px 20%;
    display: flex;
    flex-direction: column ;
    gap: 10px;
}

.main6 .background .email .email_box button:hover{
    background-color: rgb(236, 189, 58)
}
.main6 .background .email .email_box button{
    border: 0px;
    font-size: 18px;
    padding: 12px 0px;
    border-radius: 30px;
    background-color: rgba(245, 217, 140, 1);
}

.main6 .background .email .email_box input{
    border: 0px;
    background-color: rgba(244,244,245,1);
    padding: 10px 10px;
    border-radius: 10px;
}

.main6 .background .email .email_box p{
    color: rgba(113,113,122,1)
}