.main2{
    display: flex;
    justify-content: center;
    padding: 50px 0px;
    background-color: rgb(255, 255, 255);
}

.main2>div {
    display: flex;
}

.main2 .place1{
    display: flex;
    gap:20px;
}

.main2 .place1 .first{
    margin-top: 20px;
}

.main2 .place1 .second{
    margin-bottom: 20px;
}

.main2 .place2 {
    display: flex;

    justify-content: center;
    flex-direction: column;
    padding-left: 100px;
}
.main2 .place2 .yellow {
    color: rgba(245, 217, 140, 1);
}

.main2 .place2 .contents{
    width:65%
}

.main2 .place2 .contents .btnListen{
    border: 0px;
    padding: 10px 30px;
    border-radius: 30px;
    background-color: rgba(245, 217, 140, 1);
}
.main2 .place2 .contents .btnListen:hover{
    background-color: rgb(236, 189, 58)
}
.main2 .place2 .contents h1{
    font-size: 36px;
}

.main2 .place2 .contents p{
    padding: 15px 0px;
}
