.footer{
    display: flex;
    justify-content: center;
    background-color: black;
    padding: 30px 0px;
}

.footer .up_part {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;
}

.footer .buttons svg{
    background-color: black;
    color:white;

    border: 0px;

}

.footer .down_part p{
    color:white;
    display: flex;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 0px;


    border-top: 1px;
    border-left: 0px;
    border-right: 0px;
    border-bottom: 0px;
    border-style: solid;
    
    border-color: white;
}

